#FloatBar{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 2.5rem;
  position: fixed;
  top: 0;
  left: 0;
  padding: 0 5%;
  z-index: 5;
  transition: all 0.3s linear;
}

#FloatBar > .iconBtns{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 3.5rem;
  height: 100%;
}

#FloatBar svg{
  font-size: 1.25rem;
  color: var(--initial);
  cursor: pointer;
  transition: all 0.3s linear;
}

#FloatBar h4{
  opacity: 0;
  font-size: 1.25rem;
  color: var(--theme);
  transition: all 0.3s linear;
}

#FloatBar.barStyle{
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(12px);
  border-bottom: 1px solid var(--lightGray);
  transition: all 0.3s linear;
}

#FloatBar.barStyle svg{
  color: var(--textBlack);
}

#FloatBar.barStyle h4{
  opacity: 1;
  color: var(--theme);
}

#FloatBar.colorStyle svg{
  color: var(--theme);
}

@media screen and (orientation: landscape) {
  #FloatBar{
    width: calc(100% - min(16%, 300px));
    height: 2rem;
    left: min(16%, 300px);
  }

  #FloatBar > .iconBtns > svg{
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  #FloatBar svg, #FloatBar h4{
    font-size: 1rem;
  }
}