#Welcome{
  width: 100%;
  height: 100vh;
  position: relative;
  margin: 0 auto;
}

#Welcome > .welcomeText{
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: min(1200px, 90%);
  height: 100vh;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}

#Welcome > .welcomeText > h2{
  font-size: 6rem;
  color: white;
  transform: translateX(12rem);
  opacity: 0;
  margin-bottom: 0rem;
  text-shadow: 128px 0px 0px var(--special-shadow);
  animation: titleSildeIn 1s linear calc(0.5s * var(--animation-delay)) forwards;
  transition: font-size 0.2s linear;

  --special-shadow: #adc4f0;
}

#Welcome > .welcomeText > h2:hover{
  font-size: 7rem;
  transition: font-size 0.2s linear, text-shadow 0.2s linear;
}

#Welcome > .welcomeText > .toBottomBtn{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16rem;
  height: 4rem;
  margin-top: 2rem;
  font-size: 1.25rem;
  font-weight: 600;
  border: 2px solid white;
  border-radius: 2rem;
  color: white;
  cursor: pointer;
  transition: all 0.3s linear;
}

#Welcome > .welcomeText > .toBottomBtn:hover{
  background: white;
  color: #0d1420;
  transition: all 0.3s linear;
}

#Welcome > .welcomeBackground{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100vh + 6rem);
  background: linear-gradient(to bottom right, #0d1420, #0c131d);
  z-index: -1;
  overflow: hidden;
}

#Welcome > .welcomeBackground > .swimCircle{
  position: absolute;
  border-radius: 50%;
  animation: circleSwim 24s linear infinite;
}

#Welcome > .welcomeBackground > .swimCircle:nth-child(1){
  width: min(100vh, 120vw);
  height: min(100vh, 120vw);
  top: 50vh;
  left: 30vw;
  filter: blur(11rem);
  background: linear-gradient(to bottom right, #526bbd, #64d7c5);
  transform-origin: 30%, 80%;
  animation-direction: reverse;
  animation-duration: 16s;
}
#Welcome > .welcomeBackground > .swimCircle:nth-child(2){
  width: min(80vh, 110vw);
  height: min(80vh, 110vw);
  top: 10vh;
  left: 60vw;
  filter: blur(10rem);
  background: linear-gradient(to bottom right, #e66633, #cf83e8);
  transform-origin: 50%, 40%;
}
#Welcome > .welcomeBackground > .swimCircle:nth-child(3){
  width: min(70vh, 90vw);
  height: min(70vh, 90vw);
  top: 20vh;
  left: 20vw;
  filter: blur(9rem);
  background: linear-gradient(to bottom right, #5c39de, #86bcf7);
  transform-origin: 70%, 60%;
  animation-duration: 32s;
}

#Welcome > .welcomeBackground > .rotatingSquare{
  position: absolute;
  border: 2px solid white;
}

#Welcome > .welcomeBackground > .rotatingSquare:nth-child(4){
  top: calc(50vh - min(30vw, 35vh));
  right: max(-30vw, -35vh);
  width: min(60vw, 70vh);
  height: min(60vw, 70vh);
  animation: squareRotate1 6s linear infinite;
}
#Welcome > .welcomeBackground > .rotatingSquare:nth-child(5){
  top: calc(50vh - min(40vw, 45vh));
  right: max(-35vw, -45vh);
  width: min(70vw, 80vh);
  height: min(70vw, 80vh);
  transform: rotate(60deg);
  animation: squareRotate2 8s linear infinite;
}
#Welcome > .welcomeBackground > .rotatingSquare:nth-child(6){
  top: calc(50vh - min(40vw, 50vh));
  right: max(-40vw, -50vh);
  width: min(80vw, 100vh);
  height: min(80vw, 100vh);
  transform: rotate(120deg);
  animation: squareRotate3 12s linear infinite;
}

@keyframes titleSildeIn {
  50%{
    opacity: 1;
    text-shadow: 64px 0px 0px var(--special-shadow);
  }
  80%{
    transform: translateX(2rem);
  }
  100%{
    opacity: 1;
    transform: none;
    text-shadow: 0px 0px 0px var(--special-shadow);
  }
}

@keyframes squareRotate1 {
  50%{
    transform: rotate(180deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

@keyframes squareRotate2 {
  50%{
    transform: rotate(-120deg);
  }
  100%{
    transform: rotate(-300deg);
  }
}

@keyframes squareRotate3 {
  50%{
    transform: rotate(300deg);
  }
  100%{
    transform: rotate(480deg);
  }
}

@keyframes circleSwim {
  30% {
    transform: rotate(60deg) translate(120%, -90%);
  }
  60% {
    transform: rotate(120deg) translate(-110%, 120%);
  }
}

@media screen and (max-width: 767px) {
  #Welcome > .welcomeText{
    align-items: center;
  }

  #Welcome > .welcomeText > h2{
    font-size: 2.75rem;
    text-shadow: 64px 0px 0px var(--special-shadow);
    transform: translateX(6rem);
    margin-bottom: 2rem;
  }

  #Welcome > .welcomeText > h2:hover{
    font-size: 3.5rem;
  }

  #Welcome > .welcomeText > .toBottomBtn{
    margin-top: 1rem;
  }

  @keyframes titleSildeIn {
    50%{
      opacity: 1;
      text-shadow: 32px 0px 0px var(--special-shadow);
    }
    80%{
      transform: translateX(1rem);
    }
    100%{
      opacity: 1;
      transform: translateX(0);
      text-shadow: 0px 0px 0px var(--special-shadow);
    }
  }
}