#Menu{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  padding: 0 max(calc(50% - 600px), 5%);
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

#Menu > .menuLink{
  width: 32%;
  height: fit-content;
  position: relative;
  color: var(--theme-color);
  margin: 1rem 0;
  border-radius: 4px;
  box-shadow: 0px 0px 32px var(--shadow);
  transform: translateY(40%);
  transition: transform calc(0.6s + 0.3s * var(--animation-delay)) ease-out;
  overflow: hidden;
}

#Menu > .menuLink.flowIn{
  transform: translateY(0%);
}

#Menu > .menuLink > img{
  width: 100%;
  vertical-align: middle;
}

#Menu > .menuLink > .menuItemInfo{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 0 1.5rem;
  color: white;
  background: var(--theme-color);
  opacity: 0.9;
  z-index: 1;
  transform: translateX(110%);
  transition: all 0.5s ease-out;
}

#Menu > .menuLink:hover > .menuItemInfo, #Menu > .menuLink.focused > .menuItemInfo{
  transform: none;
  transition: all 0.5s ease-out;
}

#Menu > .menuLink > .menuItemInfo > span{
  margin-bottom: 0.5rem;
}

#Menu > .menuLink > .menuItemInfo > .itemTitle{
  font-size: 2rem;
  font-weight: 800;
}

#Menu > .menuLink > .menuItemInfo > .itemSubtitle{
  font-size: 1.25rem;
  font-weight: 800;
}

#Menu > .menuLink > .menuItemInfo > .itemLabelGroup{
  display: flex;
  margin-bottom: 0.5rem;
}

#Menu > .menuLink > .menuItemInfo > .itemLabelGroup > .itemLabel{
  background: white;
  color: var(--theme-color);
  font-size: 0.875rem;
  margin-right: 1rem;
  box-sizing: content-box;
  padding: 0.125rem 0.5rem;
  border-radius: 4px;
}

#Menu > .menuLink > .menuItemInfo > .itemText{
  line-height: 1.75;
}

#Menu.thumbnailView{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: nowrap;
  width: min(16%, 300px);
  height: 100vh;
  position: fixed;
  padding-top: 3rem;
  padding-left: 0;
  padding-right: 0;
  margin: 0;
  top: 0;
  left: 0;
  border-right: 1px solid var(--lightGray);
}

#Menu.thumbnailView > .menuLink{
  width: 70%;
  height: fit-content;
  transform: translateY(0%);
  margin: 0; 
  filter: grayscale(1);
  opacity: 0.7;
  transition: filter 0s linear, width 0.5s linear;
}

#Menu.thumbnailView > .menuLink:not(:first-child){
  margin-top: 1.25rem;
}

#Menu.thumbnailView > .menuLink.active, #Menu.thumbnailView > .menuLink:hover{
  filter: none;
  opacity: 1;
}


@media screen and (max-width: 1023px) {
  #Menu > .menuLink{
    width: 49%;
  }
}

@media screen and (max-width: 900px) {
  #Menu > .menuLink > .menuItemInfo > .itemText{
    display: none;
  }
}

@media screen and (max-width: 767px) {
  #Menu > .menuLink{
    width: 100%;
    margin: 1rem 0;
  }
}

@media screen and (orientation: portrait) {
  #Menu.thumbnailView{
    width: min(40%, 300px);
    transition: left 0.3s linear;
  }

  #Menu.thumbnailView.hidden{
    left: max(-40%, -300px);
    transition: left 0.3s linear;
  }
}