.workIntroduction{
  width: calc(100% - min(16%, 300px));
  margin-left: min(16%, 300px);
  padding-bottom: 2rem;
}

.workIntroduction > .banner{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 12rem;
  overflow: hidden;
}

.workIntroduction > .banner > img{
  width: 100%;
}

.workIntroduction > .content{
  width: 100%;
  padding: 0.5rem max(5%, calc(50% - 450px));
  color: var(--textBlack);
}

.workIntroduction > .content > h1{
  margin: 1.5rem 0;
  color: var(--theme-color);
}

.workIntroduction > .content > .basicInfo{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: auto;
  padding-top: 1.5rem;
  margin: 1.5rem 0;
  border-top: 1px solid var(--lightGray);
}

.workIntroduction > .content > .basicInfo > div > em{
  font-style: normal;
  font-weight: 600;
}

.workIntroduction > .content > .basicInfo > div > .label{
  color: var(--theme-color);
  font-size: 0.875rem;
  font-weight: 400;
  position: relative;
  margin-left: 1.5rem;
}

.workIntroduction > .content > .basicInfo > div > .label:nth-child(2){
  margin-left: 0.5rem;
}

.workIntroduction > .content > .basicInfo > div > .label::after{
  content: "";
  position: absolute;
  top: -0.25rem;
  left: -0.5rem;
  bottom: -0.25rem;
  right: -0.5rem;
  background: var(--theme-color);
  border-radius: 4px;
  opacity: 0.2;
}

.workIntroduction > .content > .introText > p{
  line-height: 2;
  text-indent: 2rem;
  margin: 1rem 0;
}

.workIntroduction > .content > .introImage{
  width: 100%;
}

.workIntroduction > .content > .introImage > .imgName{
  display: block;
  width: fit-content;
  margin: 0 auto;
  font-size: 0.875rem;
  color: var(--textGray);
}

.workIntroduction > .content > .introImage > img{
  width: 100%;
  margin: 1rem 0;
}

.workIntroduction > .content > .externalLink{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 6rem;
  font-weight: 600;
  color: var(--theme-color);
  margin: 0 auto;
  border-bottom: 1px solid var(--theme-color);
  transition: all 0.3s linear;
}

.workIntroduction > .content > .externalLink:hover{
  transform: translateY(-15%);
  transition: all 0.2s linear;
}

@media screen and (max-width: 1079px) {
  .workIntroduction > .content > .basicInfo{
    flex-direction: column;
    align-items: flex-start;
  }

  .workIntroduction > .content > .basicInfo > div:not(:first-child){
    margin-top: 1rem;
  }
}

@media screen and (orientation: portrait) {
  .workIntroduction{
    width: 100%;
    margin-left: 0;
    transition: margin-left 0.3s linear;
  }

  .workIntroduction.shifted{
    margin-left: min(40%, 300px);
    transition: margin-left 0.3s linear;
  }
}