* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);

  --primary-colors-theme: #445d84;
  --scale-teacher-theme: #1c1c17;
  --museum-theme: #e76f51;
  --neighbourhood-theme: #ff595e;
  --cultural-palace-theme: #5aa9e6;
  --ar-park-theme: #7d88be;
  --lightGray: #dddddd;
  --textGray: #aaaaaa;
  --textBlack: #333333;
  --shadow: rgba(80, 61, 114, 0.2);

  scroll-behavior: smooth;
}

#App{
  position: relative;
  overflow-x: hidden;
}

button{
  outline: 0;
}