#Footer{
  box-sizing: content-box;
  width: 100%;
  margin-top: 1.5rem;
  padding-bottom: 1.5rem;
  color: var(--textGray);
}

#Footer > h3{
  width: min(1200px, 90%);
  font-size: 2rem;
  font-weight: 800;
  color: var(--textBlack);
  margin: 0 auto;
  border-bottom: 1px solid var(--lightGray);
}

#Footer > .personalInfo{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: min(1200px, 90%);
  height: 3rem;
  margin: 0 auto;
}

#Footer > .filingInfo{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  width: fit-content;
  color: var(--textGray);
  height: 3rem;
  font-size: 0.875rem;
}

#Footer > .filingInfo:hover{
  text-decoration: underline;
}

@media screen and (max-width: 1023px) {
  #Footer > .personalInfo{
    flex-direction: column;
    align-items: flex-start;
    height: fit-content;
  }

  #Footer > .personalInfo > span{
    margin: 0.25rem 0;
  }
}